html.dark {
  --main-color: #ffb700; /* Vn gi mu chnh ni bt */
  --main-color-white: #ffffff; /* Mu trng cho cc thnh phn ni bt */
  --main-color-red: #ff5733; /* Mu  nht  tng phn tt hn trong nn ti */
  --main-color-dark: #171c28; /* Mu nn chnh, mu ti nht */
  --main-color-grey: #9e9da8; /* Mu xm m cho nn v cc thnh phn */
  --main-color-grey-1: #1c1c1c; /* Mu xm m */
  --main-color-grey-2: #292e39; /* Mu xm ti cho cc thnh phn ph */
  --main-color-grey-3: #9e9da8; /* Mu xm nh cho cc thnh phn ph */
  --main-color-green: #84ac8d; /* Mu xanh l cy gi nguyn */
  --main-color-highlight: #0071dc; /* Mu nhn ni bt gi nguyn */
  --text-color-main-light-3: #9e9da8; /* Mu ch nh  lm ni bt mt s phn */
  --background-logo: #1c1c1c; /* Mu nn cho logo */
  --background-navbar: #292e39; /* Mu nn cho navbar */
  --box-shadow-light-main: rgba(
    255,
    255,
    255,
    0.2
  ); /* i thnh mu sng cho shadow trong nn ti */
  --background-hover-navbar: #2c3e50; /* Mu nn hover cho navbar */
  --background-color-2: #292e39; /* Mu nn ph cho cc phn */
  --box-shadow-main-1: rgba(0, 0, 0, 0.3); /* Shadow ti hn cho nn ti */
  --box-shadow-main-2: rgba(0, 0, 0, 0.5); /* Shadow ti hn cho nn ti */

  ///
  --color-background-layout: #171c28;
  --filter--icon: brightness(0) invert(1);
  --background-button: #292e39;
  --boxshadow-main: rgba(23, 28, 40, 0.4);
  --background-color-main: #1a162e; /* Mu nn chnh cho cc phn chnh */
  --background-color-main-2: #2c3e50;
  --background--color-main-3: #1c1c1c;
  --color-text: #ffff;
  --text-color-main-light-2: #fff;
  --background--footer: #292e39;
  --color-text-2: #9e9da8;
  --background-login: #292e39;
  --text-color-main-light: #fff;
  --background__breadcrumb: #171c28;
  --boxShadow__breadcrumb: rgba(23, 28, 40, 0.2);
  --text-color-breadcrumb: #9e9da8;
  --background__studyplant-item: var(--background__breadcrumb);
  --boxShadow--studyPlant-item: var(--boxShadow__breadcrumb);
  --background-active-color: #292e39;
  --shadow--bg-gift: rgba(23, 28, 40, 0.2);
  --background-bg-gift: #171c28;
  --border-color-gift: #7644e1;
  --background-gift: #171c28;
  --shadow-gift: var(--shadow--bg-gift);
  --background-linear: linear-gradient(
    347deg,
    rgba(161, 116, 255, 1) 37%,
    rgba(94, 187, 255, 1) 69%
  );
  --background-quote: #171c28;
  --background-flashcard: var(--background-quote);
  --background-default-button: #171c28;
  --background-welcome: #171c28;
  --background__lesson: #171c28;
  --boxShadow__lesson: rgba(23, 28, 40, 0.2);
  --background--color-tag: #171c28;
  --color-text-study: #9e9da8;
  //profile
  --profile-sidebar-bg: #171c28;
  --profile-sidebar-shadow: rgba(23, 28, 40, 0.2);
  --profile-hover-link-bg: #292e39;
  //account infor
  --account-info-bg: #292e39;
  --account-info-icon-bg: #171c28;
  --account-background-color-light: #292e39;

  //create-blog
  --stroke-tool-bar: #fff;
}
