html {
  --color-text-study: #333; /* t mu vn bn mc nh */
  --color-link: #ffb900; /* Mu cho lin kt */
  --line-height: 1.6; /* Chiu cao dng mc nh */
  --font-size-base: 1.1rem; /* Kch thc phng ch c bn */
  --font-size-heading: 2rem; /* Kch thc phng ch cho tiu  */
}

html.dark {
  --color-text-study: #9e9da8; /* t mu vn bn mc nh */
  --color-link: #ffb900; /* Mu cho lin kt */
  --line-height: 1.6; /* Chiu cao dng mc nh */
  --font-size-base: 1.1rem; /* Kch thc phng ch c bn */
  --font-size-heading: 3rem; /* Kch thc phng ch cho tiu  */
}

.study {
  font-family: "Pridi", serif;
  line-height: var(--line-height);
}
.study strong {
  font-weight: 700;
  color: var(--);
}
/* Cc on vn bn trong blog */
.study p {
  color: var(--color-text-study);
  font-size: var(--font-size-base);
  line-height: 1.5;
}

/* Cc tiu  trong blog */
.study h1 {
  margin: 20px 0 10px; /* t margin trn v di */
  font-size: 2rem; /* Kch thc font cho h1 */
  line-height: 1.2; /* Chiu cao dng cho tiu  */
  font-weight: 600; /*  m ch */
  color: var(--color-text); /* Mu ch */
}

.study h2 {
  margin: 20px 0 10px; /* t margin trn v di */
  font-size: 1.75rem; /* Kch thc font cho h2 */
  line-height: 1.2; /* Chiu cao dng cho tiu  */
  font-weight: 600; /*  m ch */
  color: var(--color-text); /* Mu ch */
}

.study h3 {
  margin: 20px 0 10px; /* t margin trn v di */
  font-size: 1.5rem; /* Kch thc font cho h3 */
  line-height: 1.2; /* Chiu cao dng cho tiu  */
  font-weight: 600; /*  m ch */
  color: var(--color-text); /* Mu ch */
}

.study h4 {
  margin: 20px 0 10px; /* t margin trn v di */
  font-size: 1.25rem; /* Kch thc font cho h4 */
  line-height: 1.2; /* Chiu cao dng cho tiu  */
  font-weight: 600; /*  m ch */
  color: var(--color-text); /* Mu ch */
}

.study h5,
.study h6 {
  margin: 20px 0 10px; /* t margin trn v di */
  font-size: var(--font-size-heading); /* Kch thc font cho h5, h6 */
  line-height: 1.2; /* Chiu cao dng cho tiu  */
  font-weight: 600; /*  m ch */
  color: var(--color-text); /* Mu ch */
}

/* Cc lin kt trong blog */
.study li a {
  color: var(--color-link) !important;
  text-decoration: none; /* Loi b gch chn mc nh ca lin kt */
}

.study a:hover {
  text-decoration: underline; /* Gch chn khi di chut qua lin kt */
}

/* Cc kiu cho danh sch khng c th t */
.study ul,
.study ol {
  margin: 0 0 20px; /* t margin di  to khong cch */
  padding-left: 20px; /* t padding tri cho danh sch */
}

.study li {
  list-style: disc;
  margin-top: 20px;
  margin-bottom: 10px; /* t margin di cho mi mc danh sch */
  font-size: var(--font-size-base);
  color: var(--color-text-study);
  line-height: 1.5;
}

.study p br {
  content: ""; /* To ni dung rng  c th nh ngha kiu */
  line-height: 1; /* B line-height cho <br>  khng lm tng khong cch */
  display: block; /* Bin <br> thnh block  d iu chnh */
  margin: 0; /* Khng c khong cch thm */
}
.study p img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 10px;
}
/* Responsive styles */
@media (max-width: 1200px) {
  html {
    --font-size-base: 1rem; /* Gim kch thc phng ch c bn cho mn hnh nh hn */
    --font-size-heading: 2.5rem; /* Gim kch thc phng ch tiu  cho mn hnh nh hn */
  }
}

@media (max-width: 992px) {
  .study p {
    font-size: 0.9rem; /* Gim kch thc phng ch cho on vn bn */
  }

  .study h1,
  .study h2,
  .study h3 {
    font-size: 2rem; /* Gim kch thc phng ch tiu  */
  }
}

@media (max-width: 768px) {
  .study {
    padding: 0 15px; /* Thm padding cho cc phn t blog */
  }

  .study p {
    margin-top: 8px; /* Gim margin cho on vn bn */
  }

  .study li {
    font-size: 0.8rem; /* Gim kch thc phng ch cho mc danh sch */
  }
}

@media (max-width: 576px) {
  .study h1 {
    font-size: 1.5rem; /* Gim kch thc tiu  h1 cho mn hnh rt nh */
  }

  .study h2 {
    font-size: 1.3rem; /* Gim kch thc tiu  h2 cho mn hnh rt nh */
  }

  .study h3 {
    font-size: 1.2rem; /* Gim kch thc tiu  h3 cho mn hnh rt nh */
  }

  .study p {
    font-size: 0.85rem; /* Gim kch thc phng ch cho on vn bn */
  }
}
